<template>
  <div class="register-container">

    <form action="" @submit.prevent="submit" class="comForm" v-if="!succeedShow">
      <div class="input-wrapper">
        <input type="text" v-model.trim="sendForm.idNum" :class="errorIdNum?'active':''" @focus="focus($event,'idNum')"
               @blur="blur($event,'idNum')" placeholder="身份证号">
        <i class="del" v-show="sendForm.idNum" @mousedown.stop="del('idNum')" :style="delImg"></i>
        <span class="error">{{errorIdNum}}</span>
      </div>
      <div class="input-wrapper">
        <input type="text" v-model.trim="sendForm.queryName" @focus="focus($event)" @blur="blur($event)"
               placeholder="姓名">
        <i class="del" v-show="sendForm.queryName" @mousedown.stop="del('queryName')" :style="delImg"></i>
      </div>
      <div class="input-wrapper">
        <input type="text" v-model.trim="sendForm.queryMobile" :class="errorMobile?'active':''"
               @focus="focus($event,'mobile')" @blur="blur($event,'mobile')" placeholder="手机号">
        <i class="del" v-show="sendForm.queryMobile" @mousedown.stop="del('queryMobile')" :style="delImg"></i>
        <span class="error">{{errorMobile}}</span>
      </div>
      <div class="input-wrapper msgcode-wrapper clearfix">
        <input type="text" v-model.trim="sendForm.msgCode" :class="errorMsgCode?'active':''" @focus="focus($event)"
               @blur="blur($event)" placeholder="短信验证码">
        <i class="del" v-show="sendForm.msgCode" @mousedown.stop="del('msgCode')" :style="delImg"></i>
        <span class="aboutcode" v-if="!isMessageCode" @click="getMessageCode()">发送验证码</span>
        <span class="aboutcode sendcode" v-else>{{second}}s重发</span>
      </div>
      <div class="submit-wrapper">
        <p class="register-tip">注册即视为同意<span @click="showProtocal">《会员注册协议》</span></p>
        <div class="error-tips" v-show="errorTips">{{errorTips}}</div>
        <input type="submit" value="注册" class="submit" :class="allValid? 'active':''" :disabled="!allValid">
      </div>
      <div class="go-login-tips">
        已有账号，马上
        <router-link tag="span" to="/37duc/user/smsLogin" class="register-btn"><span>登录</span></router-link>
      </div>
      <img-code v-if="imgCodeShow"
                :absTop="51"
                :absLeft="31"
                @hanleClose="hanleImgCodeClose">
        <div class="imgcode-content">
          <div class="input-wrapper">
            <input type="text" class="input-imgcode" placeholder="请输入图形验证码"
                   @focus="focus($event,'imgCode')" @blur="blur($event,'imgCode')" v-model.trim="sendForm.queryImgCode">
            <i class="del" v-show="sendForm.queryImgCode" @mousedown.stop="del('queryImgCode')" :style="delImg"></i>
            <img ref="imgCode" :src="imgCodeSrc" alt="图形验证码" class="imgcode" @click="refreshImgcode">
            <span class="error">{{errorImgCode}}</span>
          </div>
          <input type="button" class="imgcode-btn" value="确定"
                 @click="validImgcode"
                 :class="sendForm.queryImgCode.length>=4?'active':''"
                 :disabled="sendForm.queryImgCode.length<4"
          >
        </div>
      </img-code>
    </form>

    <transition name="fade">
      <base-pop-box
        :title="'会员注册协议'" v-if="protocalShow"
        :width="874"
        :height="650"
        :content="protocalCon"
        @dealClose="handleProtocalClose">
      </base-pop-box>
    </transition>
  </div>
</template>

<script>
import BasePopBox from '@/components/BasePopBox'
import imgCode from '@/components/TheImgCode.vue'
import { mobileErrorTips, idNumErrorTips } from '@/assets/js/errorTips'
import { setLocalStorage, getLocalStorage } from '@/assets/js/localStorage'
// import { uuid } from '@/assets/js/util'
import { getComFormMixin } from '@/assets/js/mixins/comForm'
import { mapMutations } from 'vuex'
import { ValidateUtil } from '@/assets/js/global'
export default {
  name: 'login',
  mixins: [getComFormMixin],
  data () {
    return {
      imgCodeSrc: '',
      succeedShow: false,
      second: 60,
      sendForm: {
        idNum: '',
        queryName: '',
        queryMobile: '',
        queryImgCode: '',
        msgCode: ''
      },
      protocalShow: false,
      protocalCon: ''

    }
  },
  mounted () {
    this._isCountDown()
    this.getProtocalCon()
    // this.imgCodeSrc = `${process.env.VUE_APP_BASE_URL}/member/register/captcha`
  },
  methods: {
    submit () {
      let postData = {
        idNum: this.sendForm.idNum,
        mobile: this.sendForm.queryMobile,
        name: this.sendForm.queryName,
        captchaId: this.sendForm.msgCode,
        hiddenCaptcha: this.uuid
      }
      this.$api.user.register(postData).then(res => {
        if (res.code === 1) {
          this.SET_TOKEN(res.data.csrfToken)
          this.SET_USER_INFO(res.data.member)
          this.$router.push({
            path: '/37duc/user/register/succeed'
          })
        } else {
          if (res.message.includes('短信验证码错误')) {
            this.errorMsgCode = res.message
          } else {
            this.errorTips = res.message
          }
        }
      })
    },
    getMessageCode () {
      if (this.errorMobile) return
      if (!this.sendForm.queryMobile) {
        this.errorMobile = mobileErrorTips.notNull
        return
      }
      this.imgCodeShow = true
      this.refreshImgcode()
      this.sendForm.queryImgCode = ''
      // this.sendCaptchaSms()
    },
    validImgcode () {
      let postData = {
        code: this.sendForm.queryImgCode
      }
      this.$api.user.registerValidImgcode(postData).then(res => {
        if (res.code === 1) {
          this.imgCodeShow = false
          this.sendCaptchaSms()
        } else {
          this.refreshImgcode()
          this.errorImgCode = res.message
        }
      })
    },
    refreshImgcode () {
      let _random = Math.random()
      this.imgCodeSrc = `${process.env.VUE_APP_BASE_URL}/member/register/captcha?v=${_random}`
    },
    sendCaptchaSms () {
      let postData = {
        idNum: this.sendForm.idNum,
        mobile: this.sendForm.queryMobile,
        captchaId: this.uuid
      }
      this.$api.user.registerSendCaptchaSms(postData).then(res => {
        this.readsecond()
      })
    },
    getProtocalCon () {
      this.$api.user.getRegisterProtocal().then((res) => {
        this.protocalCon = res.data.content
      })
    },
    showProtocal () {
      this.protocalShow = true
    },
    handleProtocalClose () {
      this.protocalShow = false
    },
    hanleImgCodeClose () {
      this.imgCodeShow = false
    },
    blur (event, name) {
      this._onBlur(event)
      switch (name) {
        case 'idNum':
          this._validIdNum()
          break
        case 'mobile':
          this._validMobile()
          break
      }
    },
    isMobileBind () {
      let postData = {
        mobile: this.sendForm.queryMobile
      }
      this.$api.user.isMobileBind(postData).then((res) => {
        if (res.code === 0) {
          // 1：未绑定 0：绑定
          this.errorMobile = mobileErrorTips.binded
        }
      })
    },
    isIdNumRegister () {
      let postData = {
        idNum: this.sendForm.idNum
      }
      this.$api.user.isIdNumRegister(postData).then((res) => {
        if (res.code === 0) {
          // 1：未注册 0：注册
          this.errorIdNum = idNumErrorTips.registered
        }
      })
    },
    setMobileSendTime () {
      let timer = setInterval(() => {
        if (this.count <= 1) {
          this.mobileSendShow = false
          this.count = 5
          clearInterval(timer)
          return
        }
        this.count--
      }, 1000)
    },
    readsecond () {
      this.isMessageCode = true
      var timer = setInterval(() => {
        if (this.second <= 1) {
          this.isMessageCode = false
          this.second = 60
          clearInterval(timer)
          return
        }
        this.second--
        setLocalStorage('registerSecond', this.second)
      }, 1000)
    },
    _isCountDown () {
      let sec = getLocalStorage('registerSecond')
      if (sec > 1) {
        this.second = sec
        this.isMessageCode = true
        this.readsecond()
      }
    },
    _validIdNum () {
      if (this.sendForm.idNum) {
        if (!ValidateUtil.idCardRegexs(this.sendForm.idNum).pass) {
          this.errorIdNum = ValidateUtil.idCardRegexs(this.sendForm.idNum).tip
          return
        }
        if (!ValidateUtil.years16Regexs(this.sendForm.idNum).pass) {
          this.errorIdNum = ValidateUtil.years16Regexs(this.sendForm.idNum).tip
          return
        }
        this.isIdNumRegister()
      }
    },
    _validMobile () {
      if (this.sendForm.queryMobile) {
        if (!ValidateUtil.mobileRegexs(this.sendForm.queryMobile).pass) {
          this.errorMobile = ValidateUtil.mobileRegexs(this.sendForm.queryMobile).tip
          return
        }
        this.isMobileBind()
      }
    },
    ...mapMutations([
      'SET_TOKEN',
      'SET_USER_INFO'
    ])
  },
  computed: {
    allValid () {
      return this.sendForm.idNum && this.sendForm.queryName &&
          this.sendForm.queryMobile && this.sendForm.msgCode && !this.errorIdNum && !this.errorMobile
    }
  },
  components: {
    BasePopBox,
    imgCode
  }

}
</script>
<style scoped lang="scss">
  @import "@/assets/styles/comForm.scss";

  .register-container {
    .comForm {
      padding-top: 14px;
    }

    .submit-wrapper {
      .submit {
        margin-top: 38px;
      }
      .error-tips {
        top: 36px;
      }
    }
    .register-tip {
      padding-top: 10px;
      font-size: 12px;
      margin-top: -24px;
      color: #A5A8A8;
      span {
        cursor: pointer;
        color: $color-active-text;
      }
    }

  }

  .fade-enter-active, .fade-leave-active {
    transition: all .5s ease
  }

  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
  {
    opacity: 0;
  }
</style>
