<template>
  <div class="imgcode-container"  :style="imgCodeStyle">
     <p class="title">图形验证码</p>
    <slot></slot>
      <i class="close" @click="close"></i>
  </div>
</template>

<script>
export default {
  name: 'BaseAlertBox',
  props: {
    absTop: {
      type: Number,
      default: 0
    },
    absLeft: {
      type: Number,
      default: 0
    }
  },
  methods: {
    close () {
      this.$emit('hanleClose')
    }
  },
  computed: {
    imgCodeStyle () {
      return {
        top: this.absTop + 'px',
        left: this.absLeft + 'px'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";
  .imgcode-container {
    position: absolute;
    z-index: 101;
    width:320px;
    height:185px;
    background: #fff;
    box-shadow: 0 8px 16px 0 rgba(219,219,219,0.84);
    border-radius: 2px;
    padding:20px;
    .title{
      font-size: 20px;
      color: $color-text;
      font-weight: bold;
    }

    .close {
      position: absolute;
      top: 16px;
      right: 17px;
      width: 22px;
      height: 22px;
      background: url("../assets/images/close.png");
      background-size: 22px 22px;
    }
  }
</style>
