<template>
      <div  class="pop-container">
          <div class="pop-wrapper" :style="popStyle">
            <p class="title">{{title}}</p>
            <div class="content" v-html="content">
            </div>
            <i class="close" @click="goClocse"></i>
          </div>
          <div class="cover" @click="goClocse"></div>
      </div>
</template>

<script>
export default {
  name: 'BaseAlertBox',
  props: {
    title: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
    width: {
      type: Number
    },
    height: {
      type: Number
    }
  },
  data () {
    return {
      wWidth: document.documentElement.clientWidth || document.body.clientWidth,
      wHeight: document.documentElement.clientHeight || document.body.clientHeight
    }
  },
  methods: {
    goClocse () {
      this.$emit('dealClose')
    }
  },
  computed: {
    popStyle () {
      return {
        width: this.width ? this.width + 'px' : '100%',
        height: this.height ? this.height + 'px' : '100%',
        left: (this.wWidth - this.width) / 2 + 'px',
        top: (this.wHeight - this.height) / 2 - 20 + 'px'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .pop-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    .pop-wrapper {
      position: relative;
      z-index: 101;
      background: #fff;
      padding: 30px 6px 0 20px;
      overflow-y: scroll;
      .title {
        font-size: 20px;
        color: $color-text;
        font-weight: bold;
        text-align: center;
        margin-bottom: 23px;
      }
      .content {
        padding-bottom: 30px;
      }
      .close {
        position: absolute;
        top: 30px;
        right: 17px;
        width: 22px;
        height: 22px;
        background: url("../assets/images/close.png");
        background-size: 22px 22px;
      }
    }
    .cover {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 100;
      background: rgba(28, 29, 29, 0.7);
    }
  }

  /*::-webkit-scrollbar-track*/
    /*{*/
      /*background-color: rgba(6, 12, 24, 0);*/
      /*-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.22);*/

    /*}*/

   /*定义滚动条高宽及背景*/
  ::-webkit-scrollbar
   {
    width: 10px;
    background: #F7F7F7;
  }
   /*定义滚动条*/
 ::-webkit-scrollbar-thumb
  {
   background: #D5D8D8;
    border-radius: 10px;
  }

</style>
